	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/9/apple-google-maps-talks-crashed-over-voice-guided-directions/index.md absPath of file >>> MarkdownRemark","html":"<p>John Paczkowski on the reasoning behind Apple using their own mapping service in iOS 6:</p>\n<blockquote>\n<p>Spoken turn-by-turn navigation has been a free service offered through Google’s Android mobile OS for a few years now. But it was never part of the deal that brought Google’s Maps to iOS. And sources say Apple very much wanted it to be. Requiring iPhone users to look directly at handsets for directions and manually move through each step — while Android users enjoyed native voice-guided instructions — put Apple at a clear disadvantage in the mobile space. And having chosen Google as its original mapping partner, the iPhone maker was now in a position where an archrival was calling the shots on functionality important to the iOS maps feature set.</p>\n</blockquote>\n<blockquote>\n<p>And this caused Apple — which typically enjoys very tight control over its products — no end of philosophical discomfort, sources say. Apple pushed Google hard to provide the data it needed to bring voice-guided navigation to iOS. But according to people familiar with Google’s thinking, the search giant, which had invested massive sums in creating that data and views it as a key feature of Android, wasn’t willing to simply hand it over to a competing platform.</p>\n</blockquote>","frontmatter":{"title":"Apple-Google Maps Talks Crashed Over Voice-Guided Directions","date":"September 27, 2012","passthroughUrl":"http://allthingsd.com/20120926/apple-google-maps-talks-crashed-over-voice-guided-directions/"}}},"pathContext":{"slug":"/blog/2012/9/apple-google-maps-talks-crashed-over-voice-guided-directions/","previous":{"fields":{"slug":"/blog/2012/9/watch-netflix-us-in-the-uk-on-the-apple-tv/"},"frontmatter":{"title":"Watch Netflix US in the UK on the Apple TV","passthroughUrl":null}},"next":{"fields":{"slug":"/blog/2012/9/google-fakes-ios-maps-attack-ad/"},"frontmatter":{"title":"Google fakes iOS maps attack ad","passthroughUrl":"http://appleinsider.com/articles/12/09/27/googles-ilost-motorola-ad-faked-an-address-to-lose-ios-6-maps"}}}}

